@charset "UTF-8";

body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $font-family-base;
}

body {
  font-size: 1rem;
  font-weight: 400;
  color: $black;
  line-height: 1.5;
  text-align: center;
  // background-color: $black;
}

body,
html {
  position: relative;
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
}

.section,
.wrapper {
  position: relative;
  width: 100%;
}

p {
  color: $black;
  font-size: $f__16;
  font-weight: 500;
  line-height: 1.2;
}

h1 {
  color: $black;
  font-size: $f__28;
  font-weight: 700;
  line-height: 1.4;
}

h2 {
  color: $black;
  font-size: $f__18;
  font-weight: 700;
  line-height: 1.2;
}

h3 {
  color: $black;
  font-size: $f__14;
  font-weight: lighter;
  line-height: 1.5;
}

label {
  display: inline-block;
  color: $black;
  font-size: $f__10;
  font-weight: lighter;
  line-height: 1.2;
}
.App {
  overflow: hidden;
}
::-webkit-scrollbar {
  width: 2px;
}

::-webkit-scrollbar-button {
  background: transparent;
  border-radius: 4px;
}

::-webkit-scrollbar-track-piece {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.4);

  // border: px solid slategrey;
}

::-webkit-scrollbar-track {
  box-shadow: transparent;
}
@media screen and (max-width: $laptop-width) {
  .App {
    font-size: 0.9em;
  }
}

@media screen and (max-width: $laptop-width-sm) {
  .App {
    font-size: 0.8em;
  }
}
@media screen and (max-width: $desktop-width) {
  .App {
    font-size: 1em;
  }
}
