.logo {
  z-index: 999;
  position: absolute;
  width: 8.85vw;
  top: 0;
  left: 0;
  transition: all 1s;
  transform-origin: 0 0;
  img {
    width: 100%;
  }
  &--home {
    top: 7.5vh;
    left: 5.4vw;
    width: 23.7vw;
  }
}

