@media screen and (max-width: $laptop-width) {
  .logo {
    &--home {
      top: 5vh;
      width: 18vw;
    }
  }
}

@media screen and (max-width: $pad-width) {
  .logo {
    // top: em(20);
    // left: em(25);
    // transform: scale(0.6);
  }
}
@media screen and (max-width: $desktop-width) {
  .logo {
    width: 32vw;
    transform: scale(1);
    &--home {
      top: 1.5em;
      left: 1.5em;
    }
  }
}
